import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React from 'react';
import { useLocalizedConfig } from '../Common/Config';

function SkillItem(props) {
    return <p>
        <a className="myresumelink" href={props.link}>
            {props.title}
        </a>
        <br />
        {props.description}
    </p>
}

function SkillSection(props) {
    const itemList = props.itemList.map((item) =>
        item.title &&
        <SkillItem link={item.link}
            title={item.title}
            description={item.description} />
    );

    const [iconType, iconName] = props.icon.split(", ");

    return <div className="col-md-4">
        <div className={`service-box wow fadeInUp ${props.mobilebgclass}`} style={{ backgroundImage: '' }} data-wow-delay="0.1s">
            <div className="service-icon">
                <FontAwesomeIcon icon={[iconType, iconName]} />
                <h2>{props.section}</h2>
            </div>
            <div className="service-content" id={props.mobilebgid}>
                <div className="service-inner">
                    <h2>{props.section}</h2>
                    {itemList}
                </div>
            </div>
        </div>
    </div>;
}

function Skill() {
    const skill = useLocalizedConfig("skill.config.json");

    return (
        <section loading="lazy" className="service-section spad" id="resume">
            <div className="container">
                <div className="section-title">
                    <h2>{skill && skill.title}</h2>
                </div>
                <div className="row">
                    {skill && skill.skills.map((item) =>
                        <SkillSection icon={item.icon} section={item.title}
                            itemList={item.items} mobilebgclass={item.mobilebgclass}
                            mobilebgid={item.mobilebgid} />
                    )}
                </div>
            </div>
        </section>
    );
}

export default Skill;