import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { useCallback, useContext, useEffect, useState } from 'react';
import '../../css/homepage.css';
import { LanguageContext } from './LanguageProvider';

function Navigation() {
    const { lang, setLang } = useContext(LanguageContext);

    const switchLanguage = useCallback(() => {
        console.log(lang);
        setLang(lang == "en" ? "zh" : "en");
    }, [lang, setLang]);

    return (
        <div className={"nav-bar"}>
            <ul>
                <li><a href="http://tomzhao.me"><FontAwesomeIcon icon="home" /></a></li>
                <li><a href="#about">{{ en: "About", zh: "关于" }[lang]}</a></li>
                <li><a href="#resume">{{ en: "News", zh: "动态" }[lang]}</a></li>
                <li><a href="#works">{{ en: "Gallery", zh: "照片库" }[lang]}</a></li>
                <li><a href="#contact">{{ en: "Contact", zh: "联系我" }[lang]}</a></li>
                <li><a href="javascript:void(0)" onClick={switchLanguage}>{{ en: "中", zh: "En" }[lang]}</a></li>
                {/* <FontAwesomeIcon icon="globe" /> */}
            </ul>
        </div>
    );
}

export default Navigation;