import React, { useEffect } from 'react';
import { useParams } from 'react-router-dom';
import GalleryHeader from '../Gallery/GalleryHeader';
import PhotoSlideShow from '../Gallery/PhotoSlideShow';
import Navigation from '../Common/Navigation';
import GalleryIntro from './GalleryIntro';
import PhotoGallery from '../Common/PhotoGallery';
import Contact from '../Common/Contact';
import Footer from '../Common/Footer';
import '../../css/homepage.css';
import { useLocalizedConfig } from '../Common/Config';

function Gallery() {
    let { id } = useParams();
    const gallery_info = useLocalizedConfig("photo.config.json");
    const gallery = gallery_info && gallery_info.covers.find(i => i.id === id);

    return gallery ? (
        <div className="myGallery">
            <Navigation />
            <GalleryHeader title={gallery.title} />
            <PhotoSlideShow folder={gallery.title} />
            <GalleryIntro gallery={gallery}/>
            <PhotoGallery />
            <Contact />
            <Footer />
        </div>
    ) : null;
}

export default Gallery;